import React from 'react'
import { Link } from 'gatsby'
import Layout from '../components/Layout'

const PricingTable = () => (
  <>
    <Layout />
    <div className="link-menu">
      <Link to="/">Home</Link>
      <Link to="/projectAbout">Projects</Link>
      <Link to="/servicePricings">Pricing</Link>
    </div>
    <div className="background">
      <div className="container">
        <div className="panel pricing-table">
          <div className="pricing-plan">
            <img src="https://s22.postimg.cc/8mv5gn7w1/paper-plane.png" alt="" className="pricing-img" />
            <h2 className="pricing-header">Simple</h2>
            <ul className="pricing-features">
              <li className="pricing-features-item">Customized report</li>
              <li className="pricing-features-item">4 Monthly Milestone meetings</li>
              <li className="pricing-features-item">Contact list</li>
            </ul>
            <span className="pricing-price">€ 1500</span>
            {/* <a href="#/" className="pricing-button">
              Sign up
            </a> */}
          </div>
          <div className="pricing-plan">
            <img src="https://s28.postimg.cc/ju5bnc3x9/plane.png" alt="" className="pricing-img" />
            <h2 className="pricing-header">Personalized</h2>
            <ul className="pricing-features">
              <li className="pricing-features-item">Customized report</li>
              <li className="pricing-features-item">8 Biweekly Milestone meetings</li>
              <li className="pricing-features-item">Detailed contact list</li>
            </ul>
            <span className="pricing-price">€ 2000</span>
            {/* <a href="#/" className="pricing-button is-featured">
              Free trial
            </a> */}
          </div>

          <div className="pricing-plan">
            <img src="https://s21.postimg.cc/tpm0cge4n/space-ship.png" alt="" className="pricing-img" />
            <h2 className="pricing-header">Advanced</h2>
            <ul className="pricing-features">
              <li className="pricing-features-item">Customized report</li>
              <li className="pricing-features-item">8 Biweekly Milestone meetings</li>
              <li className="pricing-features-item">Detailed contact list</li>
              <li className="pricing-features-item">Negotiations with local clients/local 3rd-parties</li>
            </ul>
            <span className="pricing-price">€ 2500</span>
            {/* <a href="#/" className="pricing-button">
              Free trial
            </a> */}
          </div>
        </div>
      </div>
    </div>
  </>
)

export default PricingTable
